:root {
  --white-color: #ffff;
  --gray-color: gray;
  --primary-color: #007bff;
  --accent-gray-color: #1f2d3d;
  --black-color: #000;
  --turquoise-color: #17a2b8;
  --mustard-color: #ffc107;

}

/* Colors */

/* background color*/
.layout-navbar-fixed .wrapper .sidebar-dark-primary .brand-link:not([class*=navbar]) {
  background: var(--black-color) !important;
}
[class*=sidebar-dark-] {
  background: var(--accent-gray-color) !important;
}

.d-flex{
  display: flex;
}
.clr-primary{
  color: var(--primary-color);
}
.main-footer{
  bottom: 0;
  position: fixed;
  /* width: 100%; */
}

.user-panel img {
  height: auto;
  width: 3.1rem;
}

/* side bar */
.user-panel{
  position: sticky;
  
  background: var(--accent-gray-color) !important;
  z-index: 1;
}

.main-sidebar{
  overflow-y: auto;
}
.main-sidebar .info a{
  text-decoration: none;
  color: var(--white-color);
}

/* NAVBAR */
/* .navbar-badge {
  right: -5px;
  top: 0px;
} */


/* BUTTONS */
.btn-white{
  background: var(--white-color);
  box-shadow: rgb(0 0 0 / 30%) 0px 1px 4px -1px;
  font-weight: 400;
  font-family: 'Roboto', 'Arial', 'sans-serif';
}



/* SideBar */
.nav-pills ul.nav .nav-item .nav-link{
  color: #c2c7d0;
}

.nav.collapse.show a:hover{
  background-color: rgba(255,255,255,.1);
  color: var(--white-color);
}

.nav.collapse.show a.active:hover{
  background-color: var(--primary-color);
  color: var(--white-color);
}

.nav-link.collapse-open{
  background-color: rgba(255,255,255,.1);
  color: var(--white-color);
}

.nav-link.collapse-open .fa-angle-left.right{
  transform: rotate(-90deg);
}

.nav-pills .nav-link.active{
  color: var(--white-color) !important; 
}


/* Footer */
.main-footer{
  font-size: 0.9em;
}

/* DATATABLE */
.info-box .dataTables_wrapper{
  width: 100%;
}